import { useState, useEffect } from 'react';
import { createPortal } from 'react-dom';
import { useTranslation } from 'react-i18next';
import {
  DndContext,
  DragOverlay,
  PointerSensor,
  useSensors,
  useSensor,
  closestCorners,
} from '@dnd-kit/core';
import { arrayMove } from '@dnd-kit/sortable';
import { useDispatch, useSelector } from 'react-redux';
import { changeCardOrder, moveCard } from '../../redux/cards/cardsOperations';
import {
  reorderColumns,
  getColumnsByBoard,
} from '../../redux/columns/columnsOperations';
import { columnsSelectors } from '../../redux/columns/columnsSelectors';
import Plus from 'components/Icons/Plus';
import ColumnModal from 'components/Modals/ColumnModal';
import Column from './Column';
import TaskCard from './TaskCard/TaskCard';
import { AddButton, ColumnsList, IconWrap, Wrap } from './Dashboard.styled';

const Dashboard = ({ board }) => {
  const [isAddColumnModalOpen, setIsAddColumnModalOpen] = useState(false);
  const [druggedCard, setDruggedCard] = useState(null);
  const [druggedColumn, setDruggedColumn] = useState(null);
  const [activeColumnId, setActiveColumnId] = useState(null);

  const { t } = useTranslation();
  const dispatch = useDispatch();
  const columns = useSelector(columnsSelectors.selectSortedColumns);

  useEffect(() => {
    if (board && board._id) {
      dispatch(getColumnsByBoard(board._id));
    }
  }, [board, dispatch]);

  const sensors = useSensors(
    useSensor(PointerSensor, { activationConstraint: { distance: 3 } })
  );

  const onDragStart = event => {
    const { data } = event.active;
    if (data.current?.card) {
      setDruggedCard(data.current.card);
      setActiveColumnId(data.current?.sortable?.containerId);
    } else if (data.current?.column) {
      setDruggedColumn(data.current.column);
    }
  };

  const onDragEnd = event => {
    setDruggedCard(null);
    setDruggedColumn(null);

    const { active, over } = event;
    if (!over || !active || !active.data?.current) return;

    const activeId = active.id;
    const overId = over.id;
    if (activeId === overId) return;

    // reorder columns
    if (active.data.current?.column && over.data.current?.column) {
      const from = columns.findIndex(col => col._id === activeId);
      const to = columns.findIndex(col => col._id === overId);
      if (from !== -1 && to !== -1 && board?._id) {
        const newColumns = arrayMove(columns, from, to);
        dispatch(reorderColumns({ boardId: board._id, columns: newColumns }));
      }
      return;
    }

    // cards move / reorder
    const sameContainer =
      over.data.current?.sortable &&
      active.data.current?.sortable?.containerId &&
      over.data.current?.sortable?.containerId &&
      active.data.current?.sortable.containerId ===
        over.data.current?.sortable.containerId;

    if (!sameContainer) {
      const newColumn =
        over.data.current?.sortable?.containerId ??
        (over.data.current?.column && over.data.current?.column._id);

      const oldColumn = active.data.current?.sortable?.containerId;

      if (newColumn && oldColumn) {
        dispatch(moveCard({ cardId: activeId, newColumn, oldColumn }));
      }
    } else if (
      over.data.current?.sortable?.index !== undefined &&
      activeColumnId
    ) {
      const newIndex = over.data.current.sortable.index;
      dispatch(
        changeCardOrder({
          cardId: activeId,
          columnId: activeColumnId,
          order: newIndex,
        })
      );
    }
  };

  return (
    <Wrap>
      {columns.length > 0 && (
        <DndContext
          sensors={sensors}
          collisionDetection={closestCorners}
          onDragStart={onDragStart}
          onDragEnd={onDragEnd}
          autoScroll={{}}
        >
          <ColumnsList>
            {columns.map((column, idx) => (
              <li key={column._id || column.id || `col-${idx}`}>
                <Column allColumns={columns} column={column} />
              </li>
            ))}
          </ColumnsList>

          {createPortal(
            <DragOverlay>
              {druggedCard && (
                <TaskCard
                  allColumns={columns}
                  columnId={activeColumnId}
                  card={druggedCard}
                />
              )}
              {druggedColumn && (
                <Column
                  column={druggedColumn}
                  allColumns={columns}
                  isDragging
                />
              )}
            </DragOverlay>,
            document.body
          )}
        </DndContext>
      )}

      <AddButton type="button" onClick={() => setIsAddColumnModalOpen(true)}>
        <IconWrap>
          <Plus width={14} height={14} />
        </IconWrap>
        {t('columns.addButton')}
      </AddButton>

      {isAddColumnModalOpen && (
        <ColumnModal
          variant="add"
          closeModal={() => setIsAddColumnModalOpen(false)}
        />
      )}
    </Wrap>
  );
};

export default Dashboard;
